import { render } from 'preact';
import { useState } from 'preact/hooks';

const METAFIELD_NAMESPACE = "$app:sku-custom-discount";
const METAFIELD_KEY = "function-configuration";

// Main entry point for the extension
export default async () => {
  render(<Extension />, document.body);
}

function Extension() {
  const [products, setProducts] = useState({});
  const [bulkImportText, setBulkImportText] = useState("");
  const [showBulkImport, setShowBulkImport] = useState(false);
  const [validationErrors, setValidationErrors] = useState([]);
  const [initialProducts, setInitialProducts] = useState({});
  const [globalDiscountType, setGlobalDiscountType] = useState('percentage');
  const [loading, setLoading] = useState(false);

  // Product picker handler
  const handleProductPicker = async () => {
    try {
      const selected = await shopify.resourcePicker({ type: 'product' });
      console.log("Selected products:", selected);
      
      if (selected && selected.length > 0) {
        const newProducts = {};
        selected.forEach(product => {
          const key = `product_${product.id}`;
          newProducts[key] = {
            id: product.id,
            title: product.title,
            sku: product.variants?.[0]?.sku || '',
            price: product.variants?.[0]?.price || 0,
            image: product.images?.[0]?.url,
            value: 0,
            discountType: globalDiscountType,
          };
        });
        setProducts(prev => ({ ...prev, ...newProducts }));
      }
    } catch (error) {
      console.error("Product picker error:", error);
    }
  };

  // Process bulk import
  const processBulkImport = () => {
    try {
      setValidationErrors([]);
      
      const lines = bulkImportText.trim().split('\n');
      const skuToValueMap = {};
      const validSkus = [];
      const errors = [];
      
      lines.forEach((line, index) => {
        const trimmed = line.trim();
        if (!trimmed) return;
        
        const parts = trimmed.split(/[\t,:\s]+/).filter(p => p);
        
        if (parts.length < 2) {
          errors.push(`Line ${index + 1}: Invalid format`);
          return;
        }
        
        const sku = parts[0];
        const value = parseFloat(parts[parts.length - 1]);
        
        if (!sku) {
          errors.push(`Line ${index + 1}: Missing SKU`);
        } else if (isNaN(value)) {
          errors.push(`Line ${index + 1}: Invalid discount value`);
        } else if (value < 0) {
          errors.push(`Line ${index + 1}: Discount cannot be negative`);
        } else if (globalDiscountType === 'percentage' && value > 100) {
          errors.push(`Line ${index + 1}: Percentage must be 0-100%`);
        } else {
          skuToValueMap[sku] = value;
          validSkus.push(sku);
        }
      });
      
      if (errors.length > 0) {
        setValidationErrors(errors);
        return;
      }
      
      if (validSkus.length === 0) {
        setValidationErrors(['No valid SKU-value pairs found']);
        return;
      }
      
      // For now, add products manually from bulk import
      const newProducts = {};
      validSkus.forEach(sku => {
        const key = `sku_${sku}`;
        newProducts[key] = {
          id: key,
          title: `Product ${sku}`,
          sku: sku,
          price: 0,
          value: skuToValueMap[sku],
          discountType: globalDiscountType,
        };
      });
      
      setProducts(prev => ({ ...prev, ...newProducts }));
      setBulkImportText("");
      setShowBulkImport(false);
      
    } catch (e) {
      console.error("Bulk import error:", e);
    }
  };

  // Update product value
  const updateProductValue = (key, value) => {
    const cleanValue = value.replace(/[^0-9.]/g, '');
    
    if (cleanValue === '' || cleanValue === '.') {
      setProducts(prev => ({
        ...prev,
        [key]: {
          ...prev[key],
          value: 0
        }
      }));
      return;
    }
    
    const numValue = parseFloat(cleanValue);
    
    if (!isNaN(numValue)) {
      const limitedValue = Math.min(Math.max(numValue, 0), products[key].discountType === 'percentage' ? 100 : 99999);
      setProducts(prev => ({
        ...prev,
        [key]: {
          ...prev[key],
          value: limitedValue
        }
      }));
    }
  };

  // Update product type
  const updateProductType = (key, type) => {
    setProducts(prev => ({
      ...prev,
      [key]: {
        ...prev[key],
        discountType: type
      }
    }));
  };

  // Remove product
  const removeProduct = (key) => {
    setProducts(prev => {
      const newProducts = { ...prev };
      delete newProducts[key];
      return newProducts;
    });
  };

  // Clear all products
  const clearAll = () => {
    setProducts({});
  };

  // Save configuration
  const saveMetafieldChanges = async (event) => {
    console.log('Saving configuration:', products);
    
    // Use event.waitUntil to handle async operations
    if (event && event.waitUntil) {
      event.waitUntil(
        fetch('app:save/metafield', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify({
            namespace: METAFIELD_NAMESPACE,
            key: METAFIELD_KEY,
            value: JSON.stringify({ products })
          })
        })
      );
    }
  };

  const productEntries = Object.entries(products);
  const totalProducts = productEntries.length;

  const resetForm = () => {
    setProducts(initialProducts);
    setBulkImportText("");
    setShowBulkImport(false);
    setValidationErrors([]);
  };

  const calculateFinalPrice = (product) => {
    const price = parseFloat(product.price) || 100; // Default price for demo
    const discount = parseFloat(product.value) || 0;
    
    if (product.discountType === 'percentage') {
      return (price * (1 - discount / 100)).toFixed(2);
    } else {
      return Math.max(0, price - discount).toFixed(2);
    }
  };

  if (loading) {
    return <s-spinner size="small" />;
  }

  return (
    <s-admin-block title="SKU Discount Configuration">
      <s-function-settings>
        <s-form onSubmit={saveMetafieldChanges} onReset={resetForm}>
          <s-stack gap="large">
            {/* Header */}
            <s-section>
              <s-stack gap="base">
                <s-heading>Product SKU Discount Manager</s-heading>
                <s-stack direction="inline" gap="base" alignItems="center">
                  {totalProducts > 0 && (
                    <s-badge tone="success">{totalProducts} products configured</s-badge>
                  )}
                  {validationErrors.length > 0 && validationErrors.map((error, index) => (
                    <s-badge key={index} tone={error.includes("Platform Issue") ? "critical" : "warning"}>
                      {error}
                    </s-badge>
                  ))}
                </s-stack>
              </s-stack>
            </s-section>

            <s-divider />

            {/* Actions */}
            <s-section>
              <s-stack gap="base">
                <s-stack direction="inline" gap="base" alignItems="center" justifyContent="space-between">
                  <s-stack direction="inline" gap="base">
                    <s-button variant="primary" onClick={handleProductPicker}>
                      <s-icon name="ProductsMajor" />
                      Select Products
                    </s-button>
                    
                    <s-button variant="secondary" onClick={() => setShowBulkImport(!showBulkImport)}>
                      <s-icon name="ImportMinor" />
                      Bulk Import
                    </s-button>
                  </s-stack>
                  
                  <s-stack direction="inline" gap="base" alignItems="center">
                    <s-text variant="bodySm" tone="subdued">Default:</s-text>
                    <s-button
                      variant={globalDiscountType === 'percentage' ? 'primary' : 'secondary'}
                      size="slim"
                      onClick={() => setGlobalDiscountType('percentage')}
                    >
                      % off
                    </s-button>
                    <s-button
                      variant={globalDiscountType === 'fixedAmount' ? 'primary' : 'secondary'}
                      size="slim"
                      onClick={() => setGlobalDiscountType('fixedAmount')}
                    >
                      $ off
                    </s-button>
                  </s-stack>
                </s-stack>

                {showBulkImport && (
                  <s-box padding="base" border="base" borderRadius="base">
                    <s-stack gap="base">
                      <s-text variant="bodySm">
                        Copy from Excel: SKU, discount value ({globalDiscountType === 'percentage' ? '%' : '$'})
                      </s-text>
                      <s-text-area
                        name="bulkImport"
                        value={bulkImportText}
                        onInput={(e) => setBulkImportText(e.target.value)}
                        minRows="6"
                        placeholder={`SKU123\t35\nSKU456\t40\nSKU789\t50`}
                      />
                      <s-stack direction="inline" gap="base">
                        <s-button variant="primary" onClick={processBulkImport}>Import</s-button>
                        <s-button variant="plain" onClick={() => setShowBulkImport(false)}>Cancel</s-button>
                      </s-stack>
                    </s-stack>
                  </s-box>
                )}
              </s-stack>
            </s-section>

            <s-divider />

            {/* Product Table using new Table component */}
            <s-section>
              <s-stack gap="base">
                <s-stack direction="inline" alignItems="center" justifyContent="space-between">
                  <s-text fontWeight="semiBold">Product List</s-text>
                  {totalProducts > 0 && (
                    <s-button variant="plain" tone="critical" onClick={clearAll}>
                      <s-icon name="DeleteMinor" />
                      Clear All
                    </s-button>
                  )}
                </s-stack>
                
                {productEntries.length === 0 ? (
                  <s-banner>
                    <s-text>No products added yet. Use "Select Products" or "Bulk Import" buttons.</s-text>
                  </s-banner>
                ) : (
                  <s-table variant="auto">
                    <s-table-header-row>
                      <s-table-header listSlot="primary">Product</s-table-header>
                      <s-table-header listSlot="labeled">SKU</s-table-header>
                      <s-table-header listSlot="labeled">Price</s-table-header>
                      <s-table-header listSlot="labeled">Type</s-table-header>
                      <s-table-header listSlot="labeled">Discount</s-table-header>
                      <s-table-header listSlot="secondary">Final Price</s-table-header>
                      <s-table-header>Actions</s-table-header>
                    </s-table-header-row>
                    <s-table-body>
                      {productEntries.map(([key, product]) => (
                        <s-table-row key={key}>
                          <s-table-cell>
                            <s-stack direction="inline" gap="tight" alignItems="center">
                              {product.image && (
                                <s-thumbnail source={product.image} alt={product.title} size="small" />
                              )}
                              <s-text variant="bodySm" truncate="1">{product.title}</s-text>
                            </s-stack>
                          </s-table-cell>
                          <s-table-cell>{product.sku || '—'}</s-table-cell>
                          <s-table-cell>
                            <s-badge tone="info">${parseFloat(product.price || 100).toFixed(2)}</s-badge>
                          </s-table-cell>
                          <s-table-cell>
                            <s-select
                              name={`type-${key}`}
                              value={product.discountType}
                              onChange={(e) => updateProductType(key, e.target.value)}
                            >
                              <option value="percentage">%</option>
                              <option value="fixedAmount">$</option>
                            </s-select>
                          </s-table-cell>
                          <s-table-cell>
                            <s-number-field
                              name={`discount-${key}`}
                              value={product.value?.toString() || "0"}
                              defaultValue="0"
                              onInput={(e) => updateProductValue(key, e.target.value)}
                              prefix={product.discountType === 'fixedAmount' ? '$' : ''}
                              suffix={product.discountType === 'percentage' ? '%' : ''}
                              min="0"
                              max={product.discountType === 'percentage' ? "100" : ""}
                            />
                          </s-table-cell>
                          <s-table-cell>
                            <s-badge tone="success">${calculateFinalPrice(product)}</s-badge>
                          </s-table-cell>
                          <s-table-cell>
                            <s-button variant="plain" tone="critical" onClick={() => removeProduct(key)}>
                              <s-icon name="CancelMinor" />
                            </s-button>
                          </s-table-cell>
                        </s-table-row>
                      ))}
                    </s-table-body>
                  </s-table>
                )}
              </s-stack>
            </s-section>

            {/* Summary Grid */}
            <s-section>
              <s-stack gap="base">
                <s-heading size="small">Summary</s-heading>
                <s-grid gridTemplateColumns="repeat(auto-fit, minmax(200px, 1fr))" gap="base">
                  <s-grid-item>
                    <s-box padding="base" border="base" borderRadius="base">
                      <s-stack gap="tight">
                        <s-text variant="bodySm" tone="subdued">Total Products</s-text>
                        <s-text variant="headingMd">{totalProducts}</s-text>
                      </s-stack>
                    </s-box>
                  </s-grid-item>
                  <s-grid-item>
                    <s-box padding="base" border="base" borderRadius="base">
                      <s-stack gap="tight">
                        <s-text variant="bodySm" tone="subdued">Average Discount</s-text>
                        <s-text variant="headingMd">
                          {totalProducts > 0 
                            ? (productEntries.reduce((sum, [_, p]) => sum + parseFloat(p.value || 0), 0) / totalProducts).toFixed(1) 
                            : 0}%
                        </s-text>
                      </s-stack>
                    </s-box>
                  </s-grid-item>
                  <s-grid-item gridColumn="span 2">
                    <s-box padding="base" border="base" borderRadius="base">
                      <s-query-container>
                        <s-stack 
                          direction="@container (inline-size > 400px) inline, block"
                          gap="base"
                          alignItems="center"
                        >
                          <s-text variant="bodySm" tone="subdued">
                            Discounts applied based on SKU when available
                          </s-text>
                          <s-badge tone="success">Active</s-badge>
                        </s-stack>
                      </s-query-container>
                    </s-box>
                  </s-grid-item>
                </s-grid>
              </s-stack>
            </s-section>
          </s-stack>
        </s-form>
      </s-function-settings>
    </s-admin-block>
  );
}